import React, { useState } from 'react';
import { Controls } from '../Controls';
import { Preview } from '../Preview';
import s from './Builder.module.css';

export const colors = [ 'yellow', 'green', 'purple' ];
export const elements = [ 'front', 'side', 'back' ];

export const Builder = () => {
  const defaultColor = colors[0];
  const [state, setState] = useState({
    front: defaultColor,
    back: defaultColor,
    side: defaultColor,
  });

  const [images, setImage] = useState({
    front: `${defaultColor}-${defaultColor}-${defaultColor}-front`,
    back: `${defaultColor}-${defaultColor}-${defaultColor}-back`,
  });

  const onChange = (e) => {
    const { name, value } = e.target;
    const newState = { ...state, [name]: value };

    setState(newState);
    setImage({
      front: `${newState.front}-${newState.side}-${newState.back}-front`,
      back: `${newState.front}-${newState.side}-${newState.back}-back`,
    });
  };

  const onSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <form onSubmit={onSubmit}>
      <div className={s.Builder}>
        <div>
          <Preview front={images.front} back={images.back} />
        </div>
        <div>
          <Controls state={state} onChange={onChange} onSubmit={onSubmit} />
        </div>
      </div>
    </form>
  );
};
