import React from 'react';
import { images } from './images';

import s from './Preview.module.css';

const getImage = (name) => `${name}.png`;

export const Preview = ({ front, back }) => {
  return (
    <div className={s.Preview}>
      <div>
        <img src={images[getImage(front)]} />
      </div>
      <div>
        <img src={images[getImage(back)]}/>
      </div>
    </div>
  );
};
