import React from 'react';
import { colors, elements } from '../Builder';

export const Controls = ({ state, onChange }) => (
  <>
    {elements.map((elm) => (
      <div style={{ marginBottom: '1rem' }} key={elm}>
        <div>{elm}:</div>
        {colors.map((color) => (
          <div className="form-check form-check-inline" key={color}>
            <input
              type="radio"
              name={elm}
              value={color}
              id={`${elm}-${color}`}
              className="form-check-input"
              checked={color === state[elm]}
              onChange={onChange}
            />
            <label className="form-check-label" htmlFor={`${elm}-${color}`}>{color}</label>
          </div>
        ))}
      </div>
    ))}

    <button type="submit" className="btn btn-primary">Submit</button>
  </>
);
